import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import '../styles/Contact.css'
import '../styles/Contact-responsive.css'
function Contact() {

    const { register, handleSubmit } = useForm();
    const [data, setData] = useState([]);
    const onSubmit = (event) => {
        event['id'] = Date.now();
        setData([...data, event]);
        console.log(event)
    }

    return (
        <div className="contact-container col-12 text-center" style={{ backgroundImage: 'url(assignment2/assets/images/groot.jpg)' }}>

            <form onSubmit={handleSubmit(onSubmit)}>
                <h3 className="form-head">Contact me</h3>
                <div className="form-container">
                    
                    <input {...register("id")} hidden value=""></input>
                    <label className="name-label">Name</label>
                    <div className="name-input">
                        <input className="first-input" {...register("firstname")} placeholder="first name" required />
                        <input className="last-input" {...register("lastname")} placeholder="last name" required />
                    </div>
                    <label className="email-label">Email</label>
                    <input className="email-input" {...register("email")} type="email" placeholder="example@gmail.com" required />
                    <label className="sub-label">Subject</label>
                    <select {...register("subject")}>
                        <option value="react">React</option>
                        <option value="datamining">Data Mining</option>
                    </select>
                    <button type="submit">submit</button>
                </div>
            </form>
        <div  className="sub-body">
                <h3 className="sub-head">Registered Details</h3>
                <div className="sub-data">

                    {data.map((e) => {
                        return (
                            <div key={e.id} className="sub-child text-left">
                                <h2 className="sub-text">Name&nbsp;&nbsp;      : {e.firstname} {e.lastname}</h2>
                                <h2 className="sub-text">Email&nbsp;&nbsp;&nbsp;&nbsp;:  {e.email}</h2>
                                <h2 className="sub-text">Subject&nbsp;: {e.subject}</h2>
                                <h2 className="sub-text-design">+--------------------+</h2>
                            </div>
                        )

                    })}
                </div>
            </div>
        </div>
    )
}

export default Contact;
